import { Component } from '@angular/core';
import { AuthorService } from './author.service';

@Component({
    selector : 'authors',
   template : `<h3 style="color:red;font-style: italic;font-size:22px">{{title}}</h3>
    <ul >
    <li *ngFor="let list of names">
    {{list}}
    </li>
     </ul> 
    `,
    
})
export class AuthorDetails
{
    title='list of characters';
    names;
    
    constructor(service:AuthorService)
    {
        this.names=service.getAuthors();
    }
}